import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { Events } from 'ionic-angular';

import { ApiProvider } from '../../providers/api/api';
import { LoadingController } from 'ionic-angular';
import { ModalController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';

/**
 * Generated class for the PerfilPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-perfil',
  templateUrl: 'perfil.html',
})
export class PerfilPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public storage: Storage, public events: Events, public apiProvider: ApiProvider, public loadingController:LoadingController, public modalCtrl : ModalController, private alertCtrl: AlertController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PerfilPage');
    this.getUserData();
  }


        getUserData(){

          this.apiProvider.verificarLogin()
    .then(data => {    
      console.log(data);
      data ? this.userProfile=data : this.openModal();
     });
  
      }


goRutinaP(){
     if(this.userProfile){
    this.navCtrl.setRoot('MiRutinaPage');
   }
   else{
   this.openModalLogin(); 
   }
}



    public openModal(){
    var data = { message : 'hello world' };
    var modalPage = this.modalCtrl.create('LoginPage',data);
    modalPage.present();
    }


  cerrarSesion() {
     this.storage.set(`usr_tok_datagym`, undefined);
     this.events.publish('userLogout');
     this.navCtrl.setRoot('InicioPage');
  }



}



